import Link from 'next/link';
import { Parallax } from 'react-parallax';
import { Box, Flex, Image } from 'rebass';
import './homepage.css';
import Events from '../event/events';

const MCM10 = () => (
    <div>
        <div>
            <Box width={[0.8, 0.6]} ml='auto' mr='auto' mt={50}>
                <Image src="/static/mcm10/mcm10.svg" />
            </Box>
            <Box width={[0.8, 0.6]} ml='auto' mr='auto' className='sponsorship-message'>
                <p>Dear McMurtry Students, Alumns, Affiliates, and Families,</p>
                <p>We are exited to invite you to the McMurtry College 10th Anniversary celebration! Come join us on November 1st to enjoy in McMurtry's vibrant community and experience our college's traditions while reveling fun activities and great food.</p>
                <p>As part of our commitment to continuing McMurtry's ability to provide for comfort and happiness to all members of our community we are soliciting donations to help fund the event, with all leftover funds going to the establishment of a McMurtry endowment that will be used to students for years to come!</p>
                <p>Each sponsorship comes with two free tickets, and sponsorships of the “Conch Wielder” level or above come with a flexible number of tickets.  Email <a href="mailto:mcmurtry@rice.edu">mcmurtry@rice.edu</a> for more info.  All sponsorships will go toward funding the event, with extra collections going toward the newly established Mcmurtry endowment.</p>
                <p>For those who choose not to sponsor the event, we will be charging $30 per ticket, in order to cover the cost of the event.</p>
                <p>Please RSVP by October 28th.  We look forward to seeing you this Fall!</p>
            </Box>
            <div className='parallax-scaler'>
                <Parallax bgImage='/static/mcm10/yurt.jpg' strength={400} bgImageAlt=""
                    renderLayer={percentage => {
                        document.getElementById('tickets').style.opacity = percentage + 0.5;
                    }}>
                    <div className="mcm10-link" id='tickets'>
                        <h1>Get Tickets (soon)</h1>
                    </div>
                </Parallax>
            </div>
            <div className='parallax-scaler'>
                <Parallax bgImage='/static/mcm_commons.jpg' strength={400} bgImageAlt=""
                    renderLayer={percentage => {
                        document.getElementById("sponsor").style.opacity = percentage + 0.5;
                    }}>
                    <div className="mcm10-link" id="sponsor">
                        <Link href='/10/sponsor'>
                            <h1>Become a Sponsor</h1>
                        </Link>
                    </div>
                </Parallax>
            </div>
        </div>
        <Events />
        {/* <Link href='/10/faqs'>
            <div className='faqs-btn'>
                <p>FAQs</p>
            </div>
        </Link> */}
    </div>
)

export default MCM10